body {
    background-color: rgb(57, 52, 109);
  }
  
  .app-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .movie-container {
    width: 250px;
    margin: 20px;
    background-color: rgb(82, 75, 147);
    position: relative;
  }
  
  .movie-container img {
    max-width: 100%;
  }
  
  .movie-container h4 {
    color: rgb(250, 250, 250);
    margin: 0;
    margin-right: 50px;
  }
  
  .movie-container p {
    color: rgb(250, 250, 250);
  }
  
  .movie-info {
    padding: 15px;
    justify-content: space-between;
    display: flex;
    align-items: center;
  }
  
  .detail-box h4 {
    color: rgb(255, 255, 255);
  }
  
  .detail-box p {
    color: rgb(255, 255, 255);
  }
  
  /* 초기에는 숨김 상태로 설정 */
  .detail-box {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    color: #fff;
    padding: 10px;
  }
  
  /* 마우스 호버 시 .overview 클래스 보이도록 */
  .movie-container:hover .detail-box  {
    display: block;
  }
  